"use client";
import { use } from "react";

export default function Users({
  users,
}: {
  users: Promise<{ username: string; email: string }[]>;
}) {
  const allUsers = use(users);

  console.log(allUsers[0].username);

  return (
    <ul>
      {allUsers.map((user, index) => (
        <li key={index}>
          {user.username}---{user.email}
        </li>
      ))}
    </ul>
  );
}
